* {
  box-sizing: border-box;
}

.page {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.clock {
  border: 5px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.clock__point {
  background-color: #ff0;
  border-radius: 50%;
  width: 5.71429vmin;
  height: 5.71429vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.clock__minute {
  transform-origin: bottom;
  z-index: -1;
  background-color: green;
  width: 2.5vmin;
  height: 40%;
  animation: 5s linear infinite rotate;
  position: absolute;
  top: 10%;
  left: 48.3%;
}

.clock__hour {
  transform-origin: bottom;
  z-index: -2;
  background-color: #00f;
  width: 2.85714vmin;
  height: 25%;
  animation: 60s linear infinite rotate;
  position: absolute;
  top: 25%;
  left: 48.3%;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

